import React from 'react'
import Case from '../components/Case'
const cases = require('../data.json');

export default class CaseContainer extends React.Component{
  constructor(props){
    super(props);
  }

  render(){
    return (
    <div className="home-case">
      <div className="container">
      <h3 className="tz-title-3"><i className="fa fa-codepen" aria-hidden="true"></i>Apps</h3>
      <h4>IOSAndriodWebAppNative App, Hybrid APPReact-Native.</h4>
      <Case cases={cases.apps} type={'apps'}></Case>
      <hr/>
      <h3 className="tz-title-3"><i className="fa fa-html5" aria-hidden="true"></i>H5 Pages</h3>
      <h4>H5APPAPP/</h4>
      <Case cases={cases.h5} type={'h5'}></Case>
      <hr/>
      <h3 className="tz-title-3"><i className="fa fa-university" aria-hidden="true"></i>Websites</h3>
      <h4>,.</h4>
      <Case cases={cases.sites} type={'sites'}></Case>
    </div>
  </div>
    )
  }
}

